import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, ViewController } from 'ionic-angular';
import { Patients } from '../../../providers/patients/patients';
import { PatientPaginationMapper } from '../../../models/patient.model';
import { PatientDetailPage } from '../../index';
import { Item } from '../../../models/item';
import { LoadingProvider } from '../../../providers/loading/loading';

/**
 * Generated class for the PatientSearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'PatientSearchPage'
})
@Component({
  selector: 'page-patient-search',
  templateUrl: 'patient-search.html',
})
export class PatientSearchPage {

  searching: boolean = false;

  patientName: string;

  currentItems: PatientPaginationMapper[] = [];

  patientSelected: PatientPaginationMapper;

  page: number = 0;

  q: string = '';

  constructor(public navCtrl: NavController, private viewCtrl: ViewController, private loadingPrv: LoadingProvider,
              public patients: Patients, public modalCtrl: ModalController) { this.getItems();
  }

  getItems(params?: any) {
    return new Promise((resolve) => {
      this.loadingPrv.show('Carregando pacientes...');
      this.patients.query(params).subscribe((data: PatientPaginationMapper[]) => {
        this.loadingPrv.dismiss();
        if (params && params.q) {
          this.currentItems = data;
          resolve();
          return;
        }
        this.currentItems = this.currentItems.concat(data);
        resolve();
      }, () => {
        this.loadingPrv.dismiss();
      });
    });
  }

  getPatients(ev: any) {
    const val = ev.target.value;

    if (val === '') {
      this.q = '';
      this.page = 0;
      this.getItems({page: this.page, q: this.q});
      return;
    }

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '' && val.length > 3) {
      this.q = val;
      this.page = 0;
      this.getItems({page: this.page, q: this.q});
    }
  }

  /**
   * The view loaded, let's query our items for the list
   */
  ionViewDidLoad() {
  }

  /**
   * Prompt the user to add a new item. This shows our ItemCreatePage in a
   * modal and then adds the new item to our data source if the user created one.
   */
  addItem() {
    let addModal = this.modalCtrl.create('ItemCreatePage');
    addModal.onDidDismiss(item => {
      if (item) {
        this.patients.add(item);
      }
    });

    addModal.present();
  }

  /**
   * Delete an item from the list of items.
   */
  deleteItem(item) {
    this.patients.delete(item);
  }

  /**
   * Navigate to the detail page for this item.
   */
  openItem(item: Item) {
    this.navCtrl.push(PatientDetailPage, {
      item: item
    });
  }

  doInfinite(infiniteScroll) {
    return new Promise((resolve) => {
      console.log('doInfinite');
      this.page++;

      this.getItems({page: this.page}).then(() => {
        resolve();
        infiniteScroll.complete();
      });
    });
  }

  onCancel() {
    this.page = 0;
    this.q = '';
    this.getItems();
  }

  dismiss() {
    this.viewCtrl.dismiss({ patient: this.patientSelected });
  }


}



